import { shallow } from 'zustand/shallow';

import { useStore as useZustandStore } from '../store';

// Selectors
export const useAuth = () => useZustandStore(
  (state) => ({
    user: state.user,
    loading: state.loading,
    login: state.login,
    register: state.register,
    logout: state.logout,
    loadUser: state.loadUser,
  }),
  shallow
);

export const useTrials = () => useZustandStore(
  (state) => ({
    trials: state.trials,
    favoriteTrials: state.favoriteTrials,
    loadingTrials: state.loadingTrials,
    toggleFavorite: state.toggleFavorite,
    fetchTrials: state.fetchTrials,
  }),
  shallow
);

export const useNotifications = () => useZustandStore(
  (state) => ({
    notifications: state.notifications,
    unreadCount: state.unreadCount,
    fetchNotifications: state.fetchNotifications,
    markAsRead: state.markAsRead,
  }),
  shallow
);

export const useUI = () => useZustandStore(
  (state) => ({
    sidebarOpen: state.sidebarOpen,
    theme: state.theme,
    loadingStates: state.loadingStates,
    toggleSidebar: state.toggleSidebar,
    setTheme: state.setTheme,
    setLoading: state.setLoadingState,
  }),
  shallow
);