import React from 'react';
import { Link, useNavigate } from 'react-router-dom';

import { useAuth } from '../hooks/useStore';

import { LanguageSelector } from './LanguageSelector';

export const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleUsernameClick = () => {
    if (user?.user_type === 'company') {
      navigate('/dashboard');
    } else if (user?.user_type === 'admin') {
      navigate('/admin');
    } else {
      navigate('/profile');
    }
  };

  const handleLogout = () => {
    logout();
    navigate('/');
  };

  const handleKeyPress = (event: React.KeyboardEvent) => {
    if (event.key === 'Enter' || event.key === ' ') {
      handleUsernameClick();
    }
  };

  return (
    <div className="page-container">
      <header className="header">
        <div className="header-container container">
          <Link to="/" className="text-xl font-bold text-primary">TrialsFinder</Link>
          
          <nav className="header-nav">
            <LanguageSelector />
            {user ? (
              <div className="flex items-center gap-4">
                <span 
                  onClick={handleUsernameClick}
                  onKeyPress={handleKeyPress}
                  className="cursor-pointer text-primary font-medium transition-colors"
                  tabIndex={0}
                  role="button"
                >
                  {user.username}
                </span>
                <Link to="/privacy-dashboard" className="header-nav-item">
                  Privacy Settings
                </Link>
                <button 
                  onClick={handleLogout} 
                  className="btn" 
                  data-variant="ghost"
                  data-size="sm"
                >
                  Sign Out
                </button>
              </div>
            ) : (
              <div className="flex items-center gap-4">
                <Link to="/login" className="header-nav-item">Login</Link>
                <Link to="/register" className="btn" data-variant="primary" data-size="sm">
                  Register
                </Link>
              </div>
            )}
          </nav>
        </div>
      </header>
      
      <main className="main-content">
        <div className="container">
          {children}
        </div>
      </main>
      
      <footer className="footer">
        <div className="container">
          <div className="footer-content">
            <div className="footer-section">
              <h4>Resources</h4>
              <div className="footer-links">
                <Link to="/faq" className="footer-link">FAQ</Link>
                <Link to="/contact" className="footer-link">Contact</Link>
                <Link to="/about" className="footer-link">About</Link>
              </div>
            </div>
            <div className="footer-section">
              <h4>Legal</h4>
              <div className="footer-links">
                <Link to="/terms" className="footer-link">Terms</Link>
                <Link to="/privacy" className="footer-link">Privacy</Link>
              </div>
            </div>
          </div>
          <div className="footer-bottom">
            <p className="text-muted text-sm">© 2024 TrialsFinder. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
};