import { useEffect, lazy, Suspense } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

import './i18n';
import { CookieConsent } from './components/CookieConsent';
import { ErrorBoundary } from './components/ErrorBoundary';
import { Layout } from './components/Layout';
import { LoadingState } from './components/LoadingState';
import { PrivateRoute } from './components/PrivateRoute';
import { AppProvider } from './context/AppContext';
import { useTranslation } from './i18n';
import HomePage from './pages/HomePage';
import LoginPage from './pages/LoginPage';
import { useStore } from './store';
import { setupGlobalErrorHandlers } from './utils/errors';
import { logger } from './utils/logger';

// Lazy load non-critical pages
const RegisterPage = lazy(() => import('./pages/RegisterPage'));
const UserProfilePage = lazy(() => import('./pages/UserProfilePage'));
const CompanyDashboardPage = lazy(() => import('./pages/CompanyDashboardPage'));
const TrialDetailsPage = lazy(() => import('./pages/TrialDetailsPage'));
const SearchResultsPage = lazy(() => import('./pages/SearchResultsPage'));
const AnalyticsPage = lazy(() => import('./pages/AnalyticsPage'));
const AdminDashboardPage = lazy(() => import('./pages/AdminDashboardPage'));

// Lazy load rarely used pages
const CompanyProfilePage = lazy(() => import('./pages/CompanyProfilePage'));
const CreateTrialPage = lazy(() => import('./pages/CreateTrialPage'));
const SubscriptionPage = lazy(() => import('./pages/SubscriptionPage'));
const ForgotPasswordPage = lazy(() => import('./pages/ForgotPasswordPage'));
const EmailVerificationPage = lazy(() => import('./pages/EmailVerificationPage'));
const PaymentSuccessPage = lazy(() => import('./pages/PaymentSuccessPage'));
const ContactPage = lazy(() => import('./pages/ContactPage'));
const FAQPage = lazy(() => import('./pages/FAQPage'));
const AboutPage = lazy(() => import('./pages/AboutPage'));
const TermsPage = lazy(() => import('./pages/TermsPage'));
const PrivacyPage = lazy(() => import('./pages/PrivacyPage'));
const HelpMenu = lazy(() => import('./components/HelpMenu'));
const PrivacyDashboard = lazy(() => import('./pages/PrivacyDashboard'));

// Only setup error handlers in non-test environments
if (process.env.NODE_ENV !== 'test') {
  setupGlobalErrorHandlers();
  logger.setLevel(process.env.NODE_ENV === 'production' ? 'WARN' : 'DEBUG');
}

function App() {
  const { i18n } = useTranslation();
  const loadUser = useStore((state) => state.loadUser);
  
  useEffect(() => {
    // Only load user in non-test environments
    if (process.env.NODE_ENV !== 'test') {
      void loadUser();
    }
    // Set initial RTL direction
    document.documentElement.dir = ['ar', 'he'].includes(i18n.language) ? 'rtl' : 'ltr';
  }, [loadUser, i18n.language]);

  return (
    <ErrorBoundary>
      <AppProvider>
        <Router>
          <Layout>
            <Suspense fallback={<LoadingState fullScreen message="Loading..." />}>
              <Routes>
                <Route path="/" element={<HomePage />} />
                <Route path="/login" element={<LoginPage />} />
                <Route path="/register" element={<RegisterPage />} />
                <Route path="/forgot-password" element={<ForgotPasswordPage />} />
                <Route path="/verify-email" element={<EmailVerificationPage />} />
                <Route path="/trials/:id" element={<TrialDetailsPage />} />
                <Route path="/search" element={<SearchResultsPage />} />
                <Route path="/contact" element={<ContactPage />} />
                <Route path="/faq" element={<FAQPage />} />
                <Route path="/about" element={<AboutPage />} />
                <Route path="/terms" element={<TermsPage />} />
                <Route path="/privacy" element={<PrivacyPage />} />
                <Route path="/pricing" element={<RegisterPage />} />
                
                <Route path="/profile" element={
                  <PrivateRoute userType="user">
                    <UserProfilePage />
                  </PrivateRoute>
                } />
                
                <Route path="/privacy-dashboard" element={
                  <PrivateRoute>
                    <PrivacyDashboard />
                  </PrivateRoute>
                } />
                
                <Route path="/dashboard" element={
                  <PrivateRoute userType="company">
                    <CompanyDashboardPage />
                  </PrivateRoute>
                } />
                <Route path="/company-profile" element={
                  <PrivateRoute userType="company">
                    <CompanyProfilePage />
                  </PrivateRoute>
                } />
                <Route path="/create-trial" element={
                  <PrivateRoute userType="company">
                    <CreateTrialPage />
                  </PrivateRoute>
                } />
                <Route path="/subscription" element={
                  <PrivateRoute userType="company">
                    <SubscriptionPage />
                  </PrivateRoute>
                } />
                <Route path="/payment/:trialId" element={
                  <PrivateRoute userType="company">
                    <SubscriptionPage />
                  </PrivateRoute>
                } />
                <Route path="/payment-success" element={
                  <PrivateRoute userType="company">
                    <PaymentSuccessPage />
                  </PrivateRoute>
                } />
                <Route path="/analytics" element={
                  <PrivateRoute userType="company">
                    <AnalyticsPage />
                  </PrivateRoute>
                } />
                
                <Route path="/admin" element={
                  <PrivateRoute userType="admin">
                    <AdminDashboardPage />
                  </PrivateRoute>
                } />
              </Routes>
              <HelpMenu />
              <CookieConsent />
            </Suspense>
          </Layout>
        </Router>
      </AppProvider>
    </ErrorBoundary>
  );
}

export default App;