import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { useTrials } from '../hooks/useStore';
import { analyticsAPI } from '../api/analytics';
import { debounce, lazyLoadImages } from '../utils/performance';
import { LoadingState, LoadingSkeleton } from '../components/LoadingState';
import { withErrorBoundary } from '../components/ErrorBoundary';
import { useAsyncState } from '../hooks/useAsyncState';

const HomePage: React.FC = () => {
  const { trials, fetchTrials, loadingTrials } = useTrials();
  const [searchQuery, setSearchQuery] = useState('');
  const { data: featuredLoaded, execute } = useAsyncState<boolean>();

  const executeLoadTrials = useCallback(async () => {
    await execute(async () => {
      await fetchTrials({ page_size: 100 });
      return true;
    });
  }, [fetchTrials, execute]);

  useEffect(() => {
    executeLoadTrials();
    // Only lazy load images in non-test environments
    if (process.env.NODE_ENV !== 'test') {
      setTimeout(lazyLoadImages, 100);
    }
  }, [executeLoadTrials]);

  const debouncedSearch = useMemo(
    () => debounce((query: string) => {
      if (query.length > 2) {
        // Use location.assign for tests to avoid jsdom navigation errors
        if (process.env.NODE_ENV === 'test') {
          window.location.href = `/search?q=${encodeURIComponent(query)}`;
        } else {
          window.location.href = `/search?q=${encodeURIComponent(query)}`;
        }
      }
    }, 300),
    []
  );

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchQuery(value);
    debouncedSearch(value);
  };

  const handleViewDetails = async (trialId: number) => {
    analyticsAPI.trackEvent({
      event_type: 'trial_view',
      trial: trialId
    }).catch(() => {});
    
    if (process.env.NODE_ENV === 'test') {
      window.location.href = `/trials/${trialId}`;
    } else {
      window.location.href = `/trials/${trialId}`;
    }
  };

  const trialsArray = useMemo(() => Array.isArray(trials) ? trials : [], [trials]);
  
  const { featuredTrials, latestTrials } = useMemo(() => ({
    featuredTrials: trialsArray.filter(t => t.is_featured).slice(0, 6),
    latestTrials: trialsArray.filter(t => !t.is_featured).slice(0, 10)
  }), [trialsArray]);

  if (loadingTrials && !featuredLoaded) {
    return <LoadingState fullScreen message="Loading trials..." />;
  }

  return (
    <div className="container py-8">
      <div className="text-center mb-12">
        <h1 className="text-5xl font-bold mb-6 text-primary">Find Your Perfect Trial</h1>
        <p className="text-xl text-muted mb-8">Discover free trials for the tools and services your business needs</p>
        
        <div className="max-w-full mx-auto mb-8" style={{ maxWidth: '600px' }}>
          <div className="relative">
            <input
              type="text"
              value={searchQuery}
              onChange={handleSearchChange}
              placeholder="Search trials..."
              className="form-control form-control-lg px-6"
              style={{ paddingLeft: '3rem' }}
            />
            <span className="absolute left-4 top-1/2 transform -translate-y-1/2 text-muted">
              🔍
            </span>
          </div>
        </div>

        <Link to="/pricing" className="btn" data-variant="outline" data-size="lg">
          View Pricing Plans
        </Link>
      </div>

      {loadingTrials ? (
        <LoadingSkeleton count={6} />
      ) : (
        <>
          {featuredTrials.length > 0 && (
            <section className="mb-12">
              <h2 className="text-3xl font-semibold mb-6">Featured Free Trials</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {featuredTrials.map(trial => (
                  <div key={trial.id} className="card hover:shadow-xl transition-all">
                    <div className="card-body">
                      <div className="flex items-start justify-between mb-3">
                        <h3 className="text-xl font-semibold">{trial.title}</h3>
                        <span className="badge" data-variant="primary">Featured</span>
                      </div>
                      <p className="text-muted mb-4">{trial.description.substring(0, 150)}...</p>
                      <div className="flex items-center justify-between text-sm text-muted mb-4">
                        <span>📍 {trial.location}</span>
                        <span>🏢 {trial.company_name}</span>
                      </div>
                      <button 
                        onClick={() => handleViewDetails(trial.id)}
                        className="btn w-full" 
                        data-variant="primary"
                      >
                        View Details
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </section>
          )}

          {latestTrials.length > 0 && (
            <section className="mb-12">
              <h2 className="text-3xl font-semibold mb-6">Latest Free Trials</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {latestTrials.map(trial => (
                  <div key={trial.id} className="card hover:shadow-lg transition-all">
                    <div className="card-body">
                      <h3 className="text-xl font-semibold mb-3">{trial.title}</h3>
                      <p className="text-muted mb-4">{trial.description.substring(0, 150)}...</p>
                      <div className="flex items-center justify-between text-sm text-muted mb-4">
                        <span>🏭 {trial.industry_name}</span>
                        <span>📅 {new Date(trial.created_at).toLocaleDateString()}</span>
                      </div>
                      <button 
                        onClick={() => handleViewDetails(trial.id)}
                        className="btn w-full" 
                        data-variant="outline"
                      >
                        View Details
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </section>
          )}

          {trialsArray.length === 0 && featuredLoaded && (
            <div className="text-center py-12">
              <div className="text-6xl mb-4">🔍</div>
              <p className="text-xl text-muted">No trials available at the moment.</p>
              <p className="text-lg text-muted">Check back soon for new opportunities!</p>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default withErrorBoundary(HomePage);